<template>
  <div class="state"  v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
    <div class="no_goods" v-if="list.length === 0">
      <img src="../../assets/no_order.png" alt="" >
    </div>
    <div v-for="item in list" v-else>
      <header class="order_h">
        <p>订单号: {{item.order_sn}}</p>
        <p style="color: #666666">已完成</p>
      </header>
      <ul>
        <li class="order_good">
          <section>
            <img :src="item.pic_url" alt="">
          </section>
          <section @click="seeInfo(item)">
            <p class="good_name">{{item.good_name}}</p>
            <div class="good_num">
              <p class="good_price">¥ {{item.good_price}}</p>
              <p>x {{item.good_num}}</p>
            </div>
          </section>
        </li>
      </ul>
      <section class="order_total" >
        <span v-if="item.order_type === 2 || item.order_type === 3">{{item.jifen}}麦粒</span>
        <span v-if="item.order_type !== 2">¥ {{item.shopmoney}}</span>
      </section>
    </div>
    <div class="more_loading" v-if="!queryLoading && list.length !== 0">
      <mt-spinner type="fading-circle"  :size="25" v-show="moreLoading&&!allLoaded"></mt-spinner>
      <span v-show="allLoaded">已全部加载</span>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      list:[],
      moreLoading: false,
      queryLoading: false,
      allLoaded: false,
      pageSize: 20,
      pageNum: 1,
      totalNum: 0,
    }
  },
  methods:{
    loadMore(){
      if(this.allLoaded){
        this.moreLoading = true;
        return;
      }
      if(this.queryLoading){
        return;
      }
      this.moreLoading = !this.queryLoading;
      this.pageNum++;
      this.getShare(this.pageNum);
    },
    seeInfo(item){
      this.$router.push('/completed/' + item.id)
    },
    getOrderList(page = 1){
      this.post('home/Order/getOrderList', {
        page: page,
        number: 20,
        type: 4
      }).then(res => {
        if (res.data.code === 10000){
          this.totalNum = res.data.info.number;
          this.list = this.list.concat(res.data.info.info);
          this.list.forEach(item => {
            item.pic_url = this.imageURL(item.pic_url)
          });
          this.allLoaded = this.list.length=== this.totalNum;
          this.moreLoading = this.allLoaded;
        }
      })
    }
  },
  mounted() {
    this.getOrderList();
  }
}
</script>
<style scoped>
  @import "../../../static/goods.css";
  .more_loading{
    padding: 0.2rem;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
  }
</style>



