<template>
    <div class="order">
      <header class="count_top header">
        <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
        <span>{{name}}</span>
      </header>
      <ul class="order_top">
          <li v-for="(list,key) in lists" @click="cutNav(key)"><span class="order_list" :class="key === 0 ? 'activeNav' : ''">{{list.name}}</span></li>
      </ul>
      <div class="order_info">
        <obligation v-if="flag == 0" class='trigger'></obligation>
        <unshipped v-else-if="flag == 1" class='trigger'></unshipped>
        <unreceived v-else-if="flag == 2" class='trigger'></unreceived>
        <completed v-else-if="flag == 3" class='trigger'></completed>
      </div>
    </div>
</template>
<script>
  import obligation from './obligationList'
  import unshipped from './unshippedList'
  import unreceived from './unreceivedList'
  import completed from './completedList'
  export default {
    data () {
      return {
        lists: [
          {name: '待付款'},
          {name: '待发货'},
          {name: '待收货'},
          {name: '已完成'},
        ],
        name:'',
        flag: 0,
        id: 0
      }
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      //清理
      clearClass(){
        let list = document.querySelectorAll('.order_list');
        list.forEach(item => {
          item.className = 'order_list';
        })
      },
      cutNav(key){
        this.name = this.lists[key].name;
        this.flag = key;
        this.clearClass();
        let list = document.querySelectorAll('.order_list');
        list[key].className = 'order_list activeNav';
      }
    },
    mounted() {
      this.id = this.$route.params.id;
      this.flag = parseInt(this.$route.params.id);
      this.name = this.lists[this.id].name;
      this.cutNav(this.id);
    },
    components: {
      obligation,
      unshipped,
      unreceived,
      completed
    }
  }
</script>
<style scoped>
@import "../../../static/header.css";
  .order {
    height: 100vh;
    background-color: #F2F2F2;
  }
  .order_top {
    width: 100%;
    height: 0.8rem;
    background-color: #FF5773;
    display: flex;
    align-items: center;
    line-height: 0.8rem;
  }
  .order_top li {
    flex: 1;
    text-align: center;
  }
  .order_list {
    display: inline-block;
    font-size: 0.28rem;
    color: rgba(255, 255, 255, 0.5);
    height: 0.8rem;
  }
  .activeNav {
    color: #FFFFFF;
    border-bottom: 2px solid #FFFFFF;
  }
  .trigger {
    animation: run 0.5s ease;
  }
  @keyframes run{
    from {
      transform: translateX(100vw);
    }
    to {
      transform: translateX(0);
    }
  }
  .order_info {
    height: calc(100vh - 1.8rem);
    overflow-y: auto;
  }
</style>



