<template>
    <div class="state">
      <div class="no_goods" v-if="list.length === 0">
        <img src="../../assets/no_order.png" alt="" >
      </div>
      <div v-for="(item, index) in list" v-else>
        <header class="order_h">
          <p>订单号: {{item.order_sn}}</p>
          <p>待付款</p>
        </header>
        <ul>
          <li class="order_good">
            <section>
              <img :src="item.pic_url" alt="">
            </section>
            <section @click="seeInfo(item)">
              <p class="good_name">{{item.good_name}}</p>
              <div class="good_num">
                <p class="good_price" v-if="item.order_type !== 2">¥ {{item.good_price}}</p>
                <p class="good_price" v-if="item.order_type === 2 || item.order_type === 3">{{item.good_zjifen}} 积分</p>
                <p>x {{item.good_num}}</p>
              </div>
            </section>
          </li>
        </ul>
        <section class="order_total">
          <span>共{{item.good_num}}件商品, 合计:</span>
          <span v-if="item.order_type === 2 || item.order_type === 3">{{item.jifen}}麦粒</span>
          <span v-if="item.order_type !== 2">¥ {{item.shopmoney}}</span>
        </section>
        <footer class="order_f">
          <p>支付时间剩余:{{item.content}}</p>
          <section>
            <a href="javascript:void(0)" @click="cancelOrder(item, index)">取消订单</a>
            <a href="javascript:void(0)" @click="pay(item)">去支付</a>
          </section>
        </footer>
      </div>
      <pop ref="pop" @show="payType"></pop>
    </div>
</template>
<script>
  import { Indicator,MessageBox,Toast} from 'mint-ui';
  import pop from  '../common/pop'
export default {
  data(){
    return {
      list:[],
      content:'',
      payInfo: {},
      index:''
    }
  },
  methods:{
    payType(val) {
      //0 余额 1 其他
      if (val === '0') {
        this.post('home/Pay/payForMoney', {
          id: this.payInfo.id,
          type: this.payInfo.type
        }).then(res => {
          if (res.data.code === 10000){
            this.$router.push('/info');
            Toast({
              message: res.data.info,
              duration: 1000
            });
          } else {
            Toast({
              message: res.data.info,
              duration: 1000
            });
          }
        })
      } else {
        let token = localStorage.getItem('token');
        location.href = `http://flm.yunkzx.com/home/Shop/order_pay?token=${token}&id=${this.payInfo.id}&type=${this.payInfo.type}`;
      }
    },
    //倒计时
    countDown(timestamp, fn) {
      let self = this;
      let timer = setInterval(function () {
        let nowTime = new Date();
        let endTime = new Date(timestamp * 1000);
        let t = endTime.getTime() - nowTime.getTime();
        if (t > 0) {
          let day = Math.floor(t / 86400000);
          let hour = Math.floor((t / 3600000) % 24);
          let min = Math.floor((t / 60000) % 60);
          let sec = Math.floor((t / 1000) % 60);
          hour = hour < 10 ? "0" + hour : hour;
          min = min < 10 ? "0" + min : min;
          sec = sec < 10 ? "0" + sec : sec;
          let format = '';
          if (day > 0) {
            format = `${day}天${hour}小时${min}分${sec}秒`;
          }
          if (day <= 0 && hour > 0) {
            format = `${hour}小时${min}分${sec}秒`;
          }
          if (day <= 0 && hour <= 0) {
            format = `${min}分${sec}秒`;
          }
          fn(format);
        } else {
          fn('过期了');
          clearInterval(timer);
        }
      }, 1000);
    },
    cancelOrder(item, index){
      MessageBox.confirm('确定取消订单吗?', '').then(action => {
        this.post('home/Order/closeOrder  ',{
          order_id: item.id
        }).then(res => {
          if (res.data.code === 10000){
            this.list.splice(index, 1);
          } else {
            Toast({
              message: res.data.info,
              duration: 1000
            });
          }
        })
      }).catch(error => {

      });
    },
    seeInfo(item){
      let url = '/obligation/' + item.id;
      this.$router.push(url)
    },
    getOrderList(page = 1){
      this.post('home/Order/getOrderList', {
        page: page,
        number: 20,
        type: 1
      }).then(res => {
         if (res.data.code === 10000){
           //全部商品
           let allList = res.data.info.info;
           allList.forEach(item => {
             item['content'] = ''
           });
           this.list = allList.filter(item => {
             return item.end_time * 1000 > Date.now();
           });
           this.list.forEach((item, index) => {
             item.pic_url = this.imageURL(item.pic_url);
             this.countDown((item.end_time - 0),
               (msg) => {
                 item.content = msg;
               });
           });
         }
      })
    },
    pay(item){
      if (item.order_type === 1 || item.order_type === 4) {
        this.$refs.pop.value = '';
        this.$refs.pop.popupVisible = true;
        this.payInfo['id'] = item.id;
        this.payInfo['type'] = item.order_type;
      } else {
        let token = localStorage.getItem('token');
        location.href = `http://flm.yunkzx.com/home/Shop/order_pay?token=${token}&id=${item.id}&type=${item.order_type}`;
      }
    },
  },
  components:{pop},
  mounted() {
    this.getOrderList();
  }
}
</script>
<style scoped>
 @import "../../../static/goods.css";
</style>



